	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"SPD Loses Big in NRW","date":"22 May 2005","slug":"/2005-05-22-spd-loses-big-in-nrw/","tags":"Y2005"},"html":"<p><a href=\"http://news.bbc.co.uk/2/hi/europe/4570105.stm\" title=\"BBC NEWS | Europe | Schroeder 'heads for poll defeat'\">SPD loses big in North Rhine-Westphalia</a>, Schr&ouml;der said to seek early Bundestag elections this fall. </p>\n<p>So what was this <a title=\"Entscheidung in NRW: Zitterwahl lockt viele B&uuml;rger in die Wahlb&uuml;ros - Politik - SPIEGEL ONLINE - Nachrichten\" href=\"http://www.spiegel.de/politik/deutschland/0,1518,357052,00.html\">\"razor close race\"</a> the polls this week were supposedly reporting?</p>"},"next":{"fields":{"title":"Germany: No Points","date":"21 May 2005","slug":"/2005-05-21-germany-no-points/"}},"previous":{"fields":{"title":"Live Journal Exchange","date":"23 May 2005","slug":"/2005-05-23-live-journal-exchange/"}}},{"node":{"fields":{"title":"Germany: No Points","date":"21 May 2005","slug":"/2005-05-21-germany-no-points/","tags":"Y2005"},"html":"<p>It's 10:30, have they started the announcing the votes at the Eurovision Song Contest yet? I'm not turning on the TV until I'm sure the music is over. I only want to watch the results.</p>\n<p><strong>Update 0:26:</strong> That title wasn't a prediction. I had no idea Germany would really finish dead last. However, I did enjoy the Ukrainian Pat Sajak clone moderator.</p>"},"next":{"fields":{"title":"SPD, tut nicht weh","date":"20 May 2005","slug":"/2005-05-20-spd-tut-nicht-weh/"}},"previous":{"fields":{"title":"SPD Loses Big in NRW","date":"22 May 2005","slug":"/2005-05-22-spd-loses-big-in-nrw/"}}},{"node":{"fields":{"title":"SPD, tut nicht weh","date":"20 May 2005","slug":"/2005-05-20-spd-tut-nicht-weh/","tags":"Y2005"},"html":"<p>The best comment I've heard on Sunday's election in NRW is the fake radio spot in today's <a href=\"http://www.radiohamburg.de/neu/audio/comedy/radio_hamburg_bundestagskantine_330.mp3\"><em>Bundestagkantine</em></a> (MP3, 2.2MB, link will probably disappear tomorrow) in the style of <a href=\"http://www.wdrmaus.de/\"><em>Sendung mit der Maus</em></a>. \"Steenbruck is already Minister-President, he knows he can't do it; the dumb R&uuml;ttgers would need to learn that for himself.\" Or something like that.</p>"},"next":{"fields":{"title":"","date":"18 May 2005","slug":"/2005-05-18-spiesser/"}},"previous":{"fields":{"title":"Germany: No Points","date":"21 May 2005","slug":"/2005-05-21-germany-no-points/"}}}],"pathPrefix":"","first":false,"last":false,"index":311,"pageCount":1101}}

